.mon_profil{
    background-color: white;
    display: flex;
    flex-direction: row;
    padding: 20px;
    border-radius: 30px;
    gap: 20px
}

.mon_profil img{
    width: 90px;
    height: 90px;
    border-radius: 30%;
    box-shadow: 2px 2px 20px 0px #00000060;
    transition: 0.3s;
    cursor: pointer;
}

.mon_profil img:hover{
    opacity: 70%;

}
.mon_profil img:active{
    opacity: 10%;
}
.profil_caracs span{
width: 30%;
display: flex;
align-items: center;
}

.profil_caracs{
    display: flex;
    flex-direction: row;
    font-size: 17px;
    align-items: center;
    justify-content: space-around;
    background-color: #ffffff40;
    border-radius: 10px;
    color: white;
    padding: 10px;
    margin-top: 20px;
}

.profil_infos{
    color: white;
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    align-items: flex-start;
    height: 100%;
    gap: 30px;
    padding: 20px
}

.profil_infos h2{
    font-size: 24px;
    line-height: 0px;
}
.profil_infos span{
    font-size: 14px;
font-weight: 400;
}

.infos{
    display: flex;
    flex-direction: column;
    gap: 0px
}

.icones_profil{
    display: flex;
    flex-direction: row;
    gap: 20px;
    margin-top: 10px;
}

.icones_profil img{
    width: 30px;
    height: 30px;
    opacity: 20%;
}

.icones_profil img.is_active{
    opacity: 100%;
}

.section_persona_gris{
    background-color: #00000010;
    padding: 20px 30px 30px 30px;
    border-radius: 30px;
}

.section_persona_gris span{
font-size: 14px;
}


.nom_prenom{
    display: flex;
    flex-direction: column;
    justify-content: center;
}