input, textarea, select{
    cursor: pointer;
}

input{
    border: none
}

/* ‚úÖ input text area*/

textarea{
    display: flex;
    flex-direction: column;
    justify-content: center;
    width: 100%;
    border-radius: 20px;    
    height: auto;
    border: none ;
    transition: 0.5s;
    border: 2px solid #398ffc00;
    cursor: pointer;
    background-color: #ffffff00;
    outline: none;

}

textarea:hover{
    background-color: #ffffff40;
}


/* üèÅ end input text area*/


/* ‚úÖ input  Number*/

input[type="number"]{
    background-color: #ffffff00;
    border: none ;
    width:30px;
    color: #ffffff;
    transition: 0.5s;
    text-align: right; 
    padding-right: 5px;
    outline: none;

}
input[type="number"]:hover{
    background-color: #ffffff50;
    border-radius: 10px ;
}

input[type="number"]::after{
    content: "ans";
    position: absolute;
    z-index: 1;
    top: 0px;
    left: 0px
}
input[type="number"]::-webkit-outer-spin-button,
input[type="number"]::-webkit-inner-spin-button {
    -webkit-appearance: none;
    margin: 0;}



/* üèÅ end input Number*/


/* ‚úÖ input  Text*/




.profil_caracs input[type="text"]{
    display: flex;
    flex-direction: column;
    justify-content: center;
    width: 100%;
    border-radius: 10px;
    transition: 0.5s;
    cursor: pointer;
    color: white;
    background-color: #ffffff00;
    outline: none;

}

.profil_caracs input:hover[type="text"]{
    background-color: #ffffff50;
    transition: 0.5s;
    
}

.nom_prenom input[type="text"]{
    display: flex;
    flex-direction: column;
    justify-content: center;
    width: 80%;
    border-radius: 10px;
    transition: 0.5s;
    cursor: pointer;
    outline: none;
}

.nom_prenom input[type="text"]:hover{
    background-color: #00000010;
}

.nom_prenom input[type="text"]:focus{
    transition: 0.5s;
    color: #398ffc;
    background-color: #00000020;
}

.infos input[type="text"]{
    display: flex;
    flex-direction: column;
    justify-content: center;
    width: 100%;
    border-radius: 10px;
    transition: 0.5s;
    cursor: pointer;
    color: white;
    background-color: #ffffff00;
    outline: none;

}

.infos input[type="text"]:hover{
    background-color: #ffffff50;

}





/* üèÅ end input Text*/

/* ‚úÖ input  Range*/


input[type="range"] {
    -webkit-appearance: none;
    margin-right: 15px;
    width: 100%;
    height: 13px;
    background: #ffffff80;
    border-radius: 15px;
    outline: none;

  }
  
  input[type="range"]::-webkit-slider-thumb {
    -webkit-appearance: none;
    height: 30px;
    width: 30px;
    border-radius: 50%;
    background: #398ffc;
    cursor: ew-resize;
    box-shadow: 2px 3px 12px 0 #398ffc90;
    transition: background .3s ease-in-out;
  }
  
  input[type=range]::-webkit-slider-runnable-track  {
    -webkit-appearance: none;
    box-shadow: none;
    border: none;
    background: transparent;
  }

  .label_range{
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
  }

  /* üèÅ end input Range*/


  /* ‚úÖ input  Select*/
  select{

    background-color: #ffffff00;
    border: none;
    color: white;
    transition: 0.5s;
    padding: 5px ;
    border-radius: 10px;
    outline: none;

  }

  select:hover{
    background-color: #ffffff30;




}

.label_range select{
    color: black;
    font-size: 14px;
    -webkit-appearance: none;
    background-color: #398ffc00;
    transition: 0.3s;
}

.label_range select:hover{
    color: white;
    font-size: 14px;
    background-color: #398ffc;
}
  /* üèÅ end input Select*/
  .addQualite {
    display: flex;
    flex-direction: row;
    align-items: center;
  }
  .addQualite input[type=text]{
        border-radius: 15px;
        outline: none;
        padding: 5px;
        width: 200px;
        height: 0px;
        transition: 0.3s;

  }

  #qualite_container:hover .addQualite input[type=text]{
    height: 30px;
    transition: 0.3s;

}


  /* üë∫ Text/font params */


  textarea,.infos input[type="text"], .infos select{
  font-size: 14px;
  font-weight: 400;
  }


 input[type="number"],select,.profil_caracs input[type="text"],.addQualite input[type=text]{
  font-size: 17px;
  font-weight: 500;
  }


  .nom_prenom input[type="text"]{
    font-size: 22px;
    font-weight: 800;
    
  }



  @media screen and (max-width: 1200px){
    textarea,.infos input[type="text"], .infos select{
        font-size: 12px;
        font-weight: 400;
        }
      
      
       input[type="number"],select,.profil_caracs input[type="text"],.addQualite input[type=text]{
        font-size: 14px;
        font-weight: 500;
        }
      
      
        .nom_prenom input[type="text"]{
          font-size: 17px;
          font-weight: 800;
          
        }
      
  }