
.qualite_liste{
    background-color: #ffffff00;
    padding: 10px 0px;
border-radius: 30px;}

  .qualite_liste button{
    width:0px;
    height: 0;
    padding: 0;
    margin: 0;
    border: none;
    border-radius: 0;
    font-size: 0px;
    transition: 0.5s;
    color: black
  }

 #qualite_container:hover .qualite_liste button{
    width:20px;
    font-size: 10px;
  }

  .addQualite{
    opacity: 0%;
    transition: 0.5s;
    margin-top: -20px;
  }

  #qualite_container:hover .addQualite{
    opacity: 100%;
    transition: 0.5s;

  }

  .qualite_liste{display: flex;
    flex-direction: row;
    gap: 10px;
    flex-wrap: wrap;

}

.qualite_liste span{
    background-color: #ffffff80;
    border-radius: 20px;
    padding: 10px;
    transition: 0.3s;
}

.qualite_liste span:hover{
    background-color: #398ffc;
color: white;
transition: 0.3s;

}

.qualite_liste span:focus{
    background-color: #b1d3ff;
transition: 0.3s;

}

.qualite_liste button:hover{
    color: white
}


.addQualite button {
    height: 0px;
    width: 0px;
    margin-left: -40px;
    border-radius: 40%;
    border: none;
    background-color: #398ffc;
    color: white;
    transition: 0.3s;
  }


#qualite_container:hover .addQualite button {
    height: 28px;
    width: 28px;

    transition: 0.3s;
  }

  .addQualite button:hover {
    transition: 0.2s;
    height: 30px;
    width: 30px;
    background-color: #1c6bd2;
    }
