main{
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    background-color: #F1F1F1F1;
    height: 100%;
    width: 100%;
    gap: 20px;
    padding: 30px;
    border-radius: 50px;
    box-sizing: border-box;
    margin: 20px 50px;
    width: 90%;
    flex-wrap: wrap;
}
*{
    font-family: Poppins;
}
section{
    display: flex;
    flex-direction: column;  
    height: 100%;
    width: 30%;
    min-width: 250px;
    gap: 20px
}

.profil_column{
    display: flex;
    flex-direction: column;  
    height: 100%;
    background-color: #398ffc;
    width: 33%;
    min-width: 300px;
    padding: 30px;
    border-radius: 30px;
    box-sizing: border-box;
}

@media screen and (max-width: 1200px){
    section{
        height: 100%;
        width: 100% !important;
        min-width: none;
    }

    main{
        flex-direction: column !important;
        height: 100%;
        width: 100%;
        gap: 20px;
        padding: 10px;
        border-radius: 40px;
        box-sizing: border-box;
        margin: 0px 0px !important;
        width: 90%;
        flex-wrap: wrap;
    }

    .profil_column{
        display: flex;
        flex-direction: column;  
        height: 100%;
        background-color: #398ffc;
        width: 100%;
        min-width: none !important;
        padding: 10px;
        border-radius: 30px;
        box-sizing: border-box;
    }
}
